import Link from "next/link";

interface GameOverOverlayProps {
  onRestart: () => void;
}

export default function GameOverOverlay({ onRestart }: GameOverOverlayProps) {
  return (
    <div style={{
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      backgroundColor: 'rgba(0, 0, 0, 0.85)',
      padding: '40px',
      borderRadius: '10px',
      border: '2px solid yellow',
      textAlign: 'center',
      width: '300px',
      boxShadow: '0 0 20px rgba(255, 255, 0, 0.5)'
    }}>
      <h2 style={{ color: "yellow", fontSize: "2em", marginBottom: "20px" }}>Game Over!</h2>
      <div style={{ display: "flex", flexDirection: "column", gap: "15px" }}>
        
        {/* Bot√£o de Jogar de Novo */}
        <button
          onClick={onRestart}
          style={{
            background: "yellow",
            color: "black",
            padding: "10px 20px",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
            fontSize: "1.1em",
            fontWeight: "bold",
            transition: "background 0.2s"
          }}
        >
          Jogar de Novo
        </button>

        {/* Link para Voltar ao Menu */}
        <Link 
          href="/"
          style={{
            color: "white",
            textDecoration: "none",
            padding: "10px 20px",
            border: "1px solid white",
            borderRadius: "5px",
            transition: "background 0.2s"
          }}
        >
          Voltar ao Menu
        </Link>
      </div>
    </div>
  );
}