"use client";

import { useEffect, useRef, useState, useCallback } from "react";
import axios from "axios";
import Link from "next/link";
import GameOverOverlay from "./GameOverOverlay"; // Importando o novo componente

export default function Jogo() {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [context, setContext] = useState<CanvasRenderingContext2D | null>(null);

  // Estados iniciais para o rein√≠cio
  const initialSnake = [{ x: 8, y: 8 }];
  const initialApple = { x: 12, y: 12 };

  const [snake, setSnake] = useState(initialSnake);
  const [apple, setApple] = useState(initialApple);
  const [direction, setDirection] = useState("RIGHT");
  const [pontos, setPontos] = useState(0);
  const [morto, setMorto] = useState(false);

  const nomeJogador =
    typeof window !== "undefined" ? localStorage.getItem("nomeJogador") : "";
  const jogadorId =
    typeof window !== "undefined" ? localStorage.getItem("jogadorId") : "";

  // Defini√ß√µes de cores e tamanho
  const box = 20;
  const canvasSize = 20;
  const snakeColor = "white"; // Cobrinha branca
  const appleColor = "yellow"; // Ma√ß√£ amarela
  const canvasBgColor = "#1a1a1a"; // Fundo cinza escuro

  // Fun√ß√£o para desenhar o corpo da cobra
  const drawSnake = useCallback((ctx: CanvasRenderingContext2D, s: { x: number, y: number }[]) => {
    ctx.fillStyle = snakeColor;
    s.forEach((segment) => {
      ctx.fillRect(segment.x * box, segment.y * box, box, box);
    });
  }, [box]);

  // Fun√ß√£o para desenhar a ma√ß√£
  const drawApple = useCallback((ctx: CanvasRenderingContext2D, a: { x: number, y: number }) => {
    ctx.fillStyle = appleColor;
    ctx.fillRect(a.x * box, a.y * box, box, box);
  }, [box]);

  // Fun√ß√£o para limpar e desenhar o canvas
  const drawGame = useCallback((ctx: CanvasRenderingContext2D, s: { x: number, y: number }[], a: { x: number, y: number }) => {
    ctx.fillStyle = canvasBgColor;
    ctx.fillRect(0, 0, box * canvasSize, box * canvasSize);
    drawSnake(ctx, s);
    drawApple(ctx, a);
  }, [box, canvasSize, canvasBgColor, drawSnake, drawApple]);

  // Hook para obter o contexto do canvas na montagem
  useEffect(() => {
    if (canvasRef.current) {
      const ctx = canvasRef.current.getContext("2d");
      if (ctx) {
        setContext(ctx);
        // Desenha o estado inicial
        drawGame(ctx, initialSnake, initialApple);
      }
    }
  }, [drawGame]);

  // Fun√ß√£o ass√≠ncrona para salvar o score
  const salvarScore = async (finalPontos: number) => {
    if (!jogadorId) return;
    try {
      await axios.put(`http://localhost:4000/jogadores/${jogadorId}/score`, {
        score: finalPontos,
      });
      console.log("Score salvo/atualizado com sucesso!");
    } catch (err) {
      console.error("Erro ao salvar score:", err);
    }
  };

  // Fun√ß√µes de Jogo
  const morrer = useCallback(() => {
    setMorto(true);
    salvarScore(pontos); // Salva o score atual
  }, [pontos]);

  // Reinicia o estado do jogo
  const reiniciarJogo = () => {
    setSnake(initialSnake);
    setApple(initialApple);
    setDirection("RIGHT");
    setPontos(0);
    setMorto(false);
  };

  const gameLoop = useCallback(() => {
    if (!context || morto) return;

    // Desenha o estado atual do jogo
    drawGame(context, snake, apple);

    // L√≥gica de Movimento
    const head = { ...snake[0] };
    if (direction === "UP") head.y -= 1;
    if (direction === "DOWN") head.y += 1;
    if (direction === "LEFT") head.x -= 1;
    if (direction === "RIGHT") head.x += 1;

    // Colis√£o com paredes
    if (head.x < 0 || head.y < 0 || head.x >= canvasSize || head.y >= canvasSize) {
      morrer();
      return;
    }

    // Colis√£o com o corpo
    for (let i = 1; i < snake.length; i++) {
      if (head.x === snake[i].x && head.y === snake[i].y) {
        morrer();
        return;
      }
    }

    let newSnake = [head, ...snake];

    // Colis√£o com a ma√ß√£
    if (head.x === apple.x && head.y === apple.y) {
      setPontos((p) => p + 10);
      let newApple;
      do {
        newApple = {
          x: Math.floor(Math.random() * canvasSize),
          y: Math.floor(Math.random() * canvasSize),
        };
      } while (newSnake.some(segment => segment.x === newApple.x && segment.y === newApple.y)); // Garante que a ma√ß√£ n√£o nas√ßa na cobra
      setApple(newApple);
    } else {
      newSnake.pop(); // Remove a cauda se n√£o comeu
    }

    setSnake(newSnake);
  }, [context, snake, direction, apple, morto, canvasSize, drawGame, morrer]);

  // Hook de controle do jogo (Intervalo e Teclas)
  useEffect(() => {
    if (morto) return;

    // Intervalo do jogo (velocidade)
    const interval = setInterval(() => {
      gameLoop();
    }, 150);

    // Controle de Teclas
    const handleKey = (e: KeyboardEvent) => {
      if (e.key === "ArrowUp" && direction !== "DOWN") setDirection("UP");
      else if (e.key === "ArrowDown" && direction !== "UP") setDirection("DOWN");
      else if (e.key === "ArrowLeft" && direction !== "RIGHT") setDirection("LEFT");
      else if (e.key === "ArrowRight" && direction !== "LEFT") setDirection("RIGHT");
    };

    document.addEventListener("keydown", handleKey);

    return () => {
      clearInterval(interval);
      document.removeEventListener("keydown", handleKey);
    };
  }, [gameLoop, morto, direction]); // Adicionei 'direction' √†s depend√™ncias para evitar bugs de dire√ß√£o

  return (
    <div style={{ textAlign: "center", backgroundColor: "#000", minHeight: "100vh", color: "white" }}>
      <h1 style={{ color: "yellow", paddingTop: 20 }}>üêç SNAKE-ONLINE</h1>
      <p style={{ color: "white" }}>Jogador: **{nomeJogador || "Convidado"}**</p>
      <p style={{ color: "white", fontSize: "1.2em", fontWeight: "bold" }}>Pontua√ß√£o: <span style={{ color: "yellow" }}>{pontos}</span></p>

      <canvas
        ref={canvasRef}
        width={box * canvasSize}
        height={box * canvasSize}
        style={{ border: "2px solid white", background: canvasBgColor, margin: "20px auto" }}
      />

      {morto && (
        <GameOverOverlay onRestart={reiniciarJogo} />
      )}
    </div>
  );
}